@mixin animate($property...) {
  transition-property: $property;
  transition-duration: $transition-duration;
  transition-timing-function: $transition-function; }

@mixin box-shadow($color) {
  box-shadow: 0 5px 15px rgba($color, .2); }

@mixin btn-reset($padding: 0) {
  appearance: none;
  background-color: transparent;
  border: none;
  display: block;
  padding: $padding;

  &:focus {
    outline: none; } }

@mixin clearfix {
  &:after {
    clear: both;
    content: '';
    display: table; } }

@mixin ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

@mixin horizontal-spacing($margin: $m-sm) {
  > * {
    margin-right: $margin;

    &:last-child {
      margin-right: 0; } } }

@mixin list-unstyled {
  list-style: none;
  padding-left: 0; }

@mixin pseudoelement {
  bottom: 0;
  content: '';
  left: 0;
  position: absolute;
  right: 0;
  top: 0; }

@mixin vertical-spacing($margin: $m-sm) {
  > * {
    margin-bottom: $margin;

    &:last-child {
      margin-bottom: 0; } } }

@mixin media-touch-devices {
  @media (any-pointer: coarse) {
    @content; } }
