@mixin type-color($color) {
  color: $color;

  &:hover {
    background-color: rgba($color, .2); }

  &.-active {
    background-color: $color; } }

.btn {
  @include animate(background-color, color);
  border: none {
    radius: $border-radius-sm; }
  cursor: pointer;
  display: inline-block;
  font: {
    family: $font-stack-body;
    size: $font-size-sm;
    weight: $font-weight-bold; }
  letter-spacing: .5px;
  line-height: $line-height-base;
  padding: $m-sm $m-md;
  text: {
    decoration: none;
    transform: uppercase; }

  &:focus {
    outline: none; }

  &:hover {
    @include media-touch-devices {
      background-color: inherit !important;
      color: inherit !important; } }

  //----- color versions -----------------------------------------------------------------------------------------------
  &.-primary {
    background-color: $brand-primary;
    color: $color-white;

    &:hover {
      background-color: rgba($brand-primary, .7); } }

  &.-default {
    background-color: $color-gray-lighter;
    color: $color-gray-dark;

    &:hover {
      background-color: $color-gray-light; }

    &.-active {
      background-color: $color-gray; } }

  &.-bug {
    @include type-color(#9cb820); }

  &.-dragon {
    @include type-color(#7038f8); }

  &.-fairy {
    @include type-color(#f09ad9); }

  &.-fire {
    @include type-color(#f08030); }

  &.-ghost {
    @include type-color(#705898); }

  &.-ground {
    @include type-color(#e0b668); }

  &.-normal {
    @include type-color(#a8a8a8); }

  &.-psychic {
    @include type-color(#eb2d77); }

  &.-steel {
    @include type-color(#6d8f9c); }

  &.-dark {
    @include type-color(#504843); }

  &.-electric {
    @include type-color(#c09643); }

  &.-fighting {
    @include type-color(#c03028); }

  &.-flying {
    @include type-color(#9096f0); }

  &.-grass {
    @include type-color(#22c02a); }

  &.-ice {
    @include type-color(#98d8d8); }

  &.-poison {
    @include type-color(#a040a0); }

  &.-rock {
    @include type-color(#b8a038); }

  &.-water {
    @include type-color(#6890f0); }

  &.-active {
    color: $color-white; } }
