.pokemon-card {
  $this: &;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  padding: ($m-base + $m-sm) $m-base $m-base;

  &:nth-of-type(even) {
    background-color: $color-gray-lighter; }

  &__img {
    align-items: center;
    display: flex;
    filter: drop-shadow(4px 4px 4px rgba($color-black, .2));
    flex: 0 0 128px;
    justify-content: center;
    min: {
      height: 96px;
      width: 96px; } }

  &__info {
    background: $color-white;
    border-radius: $border-radius-base;
    box-shadow: $m-sm $m-sm $m-xl $color-gray-light;
    flex: 0 0 300px;
    margin: {
      left: $m-base;
      right: $m-xl; }
    position: relative;
    text-align: center; }

  &__header {
    position: relative;

    #{$this}__img {
      left: 50%;
      position: absolute;
      top: 50%;
      transform: translate(-50%,-50%); } }

  &__id {
    color: $color-gray-light;
    font-size: 100px;
    font-weight: $font-weight-bold; }

  &__name {
    font: {
      size: $font-size-lg;
      weight: $font-weight-bold; }
    margin-top: -$m-sm; }

  &__genus {
    font-size: $font-size-sm; }

  &__abilities {
    margin: {
      bottom: $m-base;
      top: $m-xs; } }

  &__ability {
    font: {
      size: $font-size-sm;
      weight: $font-weight-bold; }

    & + & {
      margin-left: $m-sm; } }

  &__table {
    margin-right: $m-xl;

    td {
      text-align: right;

      &:first-child {
        color: $color-gray; }

      &:nth-child(2) {
        width: 50px; } } }

  &__graph-wrapper {
    background-color: $color-gray-light;
    border-radius: $border-radius-sm;
    overflow: hidden;
    width: 200px; }

  &__graph {
    background-color: $brand-primary;
    height: $m-sm;
    position: relative;

    &:before {
      @include pseudoelement;
      background-image: linear-gradient(70deg, rgba(255,255,255,.2), rgba(255,255,255,0)); }

    &.-low {
      background-color: $stats-low; }

    &.-medium {
      background-color: $stats-medium; }

    &.-high {
      background-color: $stats-high; }

    &.-epic {
      background-color: $stats-epic; } }

  &__details {
    align-items: center;
    display: flex; } }

.pokemon-stat {
  background-color: $color-white;
  border-radius: $border-radius-lg;
  box-shadow: $m-sm $m-sm $m-xl $color-gray-light;
  font-size: $font-size-sm;
  font-weight: $font-weight-bold;
  line-height: $m-xl;
  position: absolute;
  top: -$m-sm;
  width: $m-xl;

  &.-height {
    left: -$m-sm; }

  &.-weight {
    right: -$m-sm; } }
