@keyframes loading {
  50% {
    transform: translateY(-30%); }
  0%,
  100% {
    transform: translateY(0); } }

.pokeball {
  animation: loading both infinite 1s;
  background: linear-gradient(to bottom, #EEEEEE 0%, #FFFFFF 100%);
  border-radius: 50px;
  border: solid 2px black;
  height: 50px;
  margin: 60px auto;
  position: relative;
  width: 50px; }
  .pokeball:before, .pokeball:after {
    content: '';
    display: block; }
  .pokeball, .pokeball:before, .pokeball:after {
    box-sizing: content-box; }
  .pokeball:before {
    background: linear-gradient(to bottom, #d10000 0%, #ff0000 50%);
    border-bottom: solid 2px black;
    border-radius: 25px 25px 0 0;
    height: 24px;
    width: 50px; }
  .pokeball:after {
    background: linear-gradient(to bottom, #fff 0%, #ccc 100%);
    border-radius: 50%;
    box-shadow: 0 0 0 1px black, 0 0 0 2px #ddd, 0 0 0 3.57143px black, 0 0 5px 2.94118px rgba(0, 0, 0, 0.4);
    content: "";
    height: 10px;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 10px; }

.pokemon-card {
