//----- fonts sizes and line-heights -----------------------------------------------------------------------------------
$font-size-xxl:                   72px;
$font-size-xl:                    24px;
$font-size-lg:                    20px;
$font-size-base:                  16px;
$font-size-sm:                    13px;
$font-size-xs:                    12px;

$line-height-base:                1.25;
$line-height-sm:                  1;
$line-height-lg:                  1.5;

//----- spacings (margins/paddings) ------------------------------------------------------------------------------------
$m-base:                          20px;
$m-xs:                            $m-base / 4;
$m-sm:                            $m-base / 2;
$m-md:                            $m-base / 4 * 3;
$m-lg:                            $m-base * 2;
$m-xl:                            $m-base * 3;

//----- border radiuses ------------------------------------------------------------------------------------------------
$border-radius-base:              $m-base;
$border-radius-sm:                3px;
$border-radius-lg:                $m-lg;

//----- breakpoints ----------------------------------------------------------------------------------------------------
$grid-breakpoints:                (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px);

$grid-columns:                    12;
$grid-gutter-width:               $m-base;

$container:                       map-get($grid-breakpoints, xl);

//----- colors ---------------------------------------------------------------------------------------------------------
$brand-primary:                   #33aeaf;

$stats-low:                       #de4141;
$stats-medium:                    #f59700;
$stats-high:                      #f5c400;
$stats-epic:                      #5a9c39;


$color-black:                     #04080c;
$color-gray-dark:                 hsl(36, 5%, 38%);
$color-gray:                      hsl(36, 5%, 62%);
$color-gray-light:                hsl(36, 5%, 91%);
$color-gray-lighter:              hsl(36, 5%, 97%);
$color-white:                     #ffffff;

$placeholder-color:               $color-gray;

//----- fonts ----------------------------------------------------------------------------------------------------------
$font-family-body:                'Rubik';
$font-stack-body:                 $font-family-body, sans-serif;

$font-weight-regular:             400;
$font-weight-bold:                500;

//----- transitions ----------------------------------------------------------------------------------------------------
$transition-duration:             .3s;
$transition-function:              ease-in-out;

//----- z-indexes ------------------------------------------------------------------------------------------------------
$z-negative:                      -1;
$z-low:                           100;
$z-middle:                        200;
$z-high:                          300;
$z-higher:                        400;
